<template>
  <section>
    <div class="pt-14 pb-6 grey darken-3 white--text">
      <v-container>
        <v-row justify="center" align="center">
          <v-col cols="12" md="7">
            <h1 class="text-center">{{ $t('index.newsletter.heading') }}</h1>
            <base-divider class="mt-2 mb-10" center width="150px" />
            <base-alert display="app-newsletter" />
            <v-form ref="subscribeForm" v-model="valid" @submit.prevent="save">
              <v-text-field
                v-model="item.email"
                :rules="[rules.required, rules.isEmail]"
                outlined
                rounded
                dark
                color="primary"
                :label="$t('index.newsletter.emailInput.label')"
              >
                <template v-slot:append-outer>
                  <v-btn
                    rounded
                    height="56"
                    class="primary white--text px-8"
                    style="margin-top: -18px;"
                    :loading="$store.state.loading.newsletter"
                    type="submit"
                  >
                    {{ $t('index.newsletter.button.text') }}
                  </v-btn>
                </template>
              </v-text-field>
            </v-form>
          </v-col>
        </v-row>
      </v-container>
    </div>
  </section>
</template>

<script>
import { mapActions } from 'vuex'
import BaseAlert from './base/BaseAlert.vue'
import validations from '@/mixins/validations'
export default {
  components: { BaseAlert },
  mixins: [validations],
  data() {
    return {
      item: { email: '' }
    }
  },
  methods: {
    ...mapActions('newsletter', ['addSubscriber']),
    async save() {
      this.$refs.subscribeForm.validate()
      if (this.valid) {
        const payload = {
          subscriber: this.item,
          axios: this.$axios,
          form: this.$refs.subscribeForm,
          alertDisplay: 'app-newsletter'
        }
        await this.addSubscriber(payload)
      }
    }
  }
}
</script>

